'use client'

import { useEffect } from 'react'
import { Button } from '@/components/ui/button'
import Link from 'next/link'
import { useParams } from 'next/navigation'

export default function LessonError({
  error,
  reset,
}: {
  error: Error & { digest?: string }
  reset: () => void
}) {
  const params = useParams()
  const slug = params.slug as string

  useEffect(() => {
    console.error('Lesson error:', error)
  }, [error])

  return (
    <div className="min-h-screen flex items-center justify-center">
      <div className="text-center max-w-md mx-auto space-y-6 p-6">
        <h1 className="text-3xl font-bold text-destructive">Lesson Error</h1>
        <p className="text-muted-foreground">
          We encountered an error while loading this lesson. The lesson might not exist or there could be a technical issue.
        </p>
        <div className="flex flex-col sm:flex-row gap-3 justify-center">
          <Button onClick={reset} variant="default">
            Try Again
          </Button>
          <Button asChild variant="outline">
            <Link href={`/courses/${slug}`}>Back to Course</Link>
          </Button>
        </div>
      </div>
    </div>
  )
}